<template>
  <li class="playlistitem" @click="navigateToSongList">
    <div class="thumb">
      <img :src="item.picUrl" alt="" />
      <span class="play-count">{{ item.playCount | formatPlayCount }}</span>
    </div>
    <h6>{{ item.name }}</h6>
  </li>
</template>

<script>
export default {
  name: "RecommendPlaylist",

  props: ["item", "index", "id"],
  filters: {
    formatPlayCount: function(value) {
      return (value / 10000).toFixed(1) + "";
    },
  },
  methods: {
    navigateToSongList() {
      console.log("enter", this.id);
      // /playlist/detail?id=24381616

      this.$router.push({
        path: "/SongLists",
        query: { id: this.id },
      });
    },
  },
};
</script>

<style scoped lang="scss">
.playlistitem {
  min-width: 103px;
  min-height: 142px;
  flex: 1 1 calc(100vw / 3 - 10px);
  &:nth-of-type(3n + 2) {
    margin-right: 5px;
    margin-left: 5px;
  }
  .thumb {
    position: relative;
    img {
      width: 33vw;
      vertical-align: bottom;
    }
    .play-count {
      font-size: 12px;
      position: absolute;
      right: 5px;
      top: 2px;
      color: #fff;

      &::before {
        content: "";
        display: inline-block;
        background: url("data:image/svg+xml;base64,");
        background-position: left center;
        background-repeat: no-repeat;
        background-size: auto 100%;
        width: 1.2em;
        height: 0.9em;
      }
    }
  }
  h6 {
    font-size: 13px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 5px;
    padding-bottom: 15px;
    height: 38px;
  }
}
</style>
