<template>
  <div class="song-list">
    <div class="wraper">
      <Loading v-show="isGetAllData"></Loading>
      <keep-alive>
        <section class="list-top" v-if="accompleteIndex === 2">
          <div
            class="header-Bg-wraper"
            :style="'background-image:url(' + coverImgUrl + '?param=170y170)'"
          ></div>
          <div class="header-content-wraper">
            <div class="left">
              <img
                :src="coverImgUrl + '?imageView=1&type=webp&thumbnail=253x0'"
              />
              <span class="dec"></span>
              <span class="play-count">
                {{ playCount | formatPlayCount }}
              </span>
            </div>
            <div class="right">
              <h2>{{ name }}</h2>
              <div class="lsthd_auth ">
                <a
                  class="lsthd_link"
                  href="//music.163.com/m/user?id=516707850"
                >
                  <div class="u-avatar">
                    <img
                      class="u-img"
                      :src="
                        authorImg +
                          '?imageView=1&amp;type=webp&amp;thumbnail=61x0'
                      "
                    />
                  </div>
                  {{ authorName }}
                </a>
              </div>
            </div>
          </div>
        </section>
      </keep-alive>

      <Loading v-show="isGetAllData"></Loading>
      <section class="song-list" v-if="accompleteIndex === 2">
        <keep-alive>
          <ul class="songs">
            <SongListSong
              v-for="(item, index) in songs"
              :key="item.id"
              :index="index"
              :item="item"
              :toplist="songs"
              class="songItem"
              :isPaused="$attrs.isPaused"
              :currentSong="$attrs.currentSong"
              @update:currentSong="updateCurrentSong"
              @update:newSongList="hotSongUpLoadNowSong"
            >
            </SongListSong>
          </ul>
        </keep-alive>
      </section>
    </div>
  </div>
</template>
<!--  -->

<script>
import Loading from "@/components/Loading.vue";
import SongListSong from "@/components/SongListSong.vue";
export default {
  components: {
    Loading,
    SongListSong,
  },
  filters: {
    formatPlayCount: function(value) {
      return (value / 10000).toFixed(1) + "";
    },
  },
  data() {
    return {
      songs: [],
      isGetAllData: true,
      name: null,
      coverImgUrl: null,
      authorName: null,
      authorImg: null,
      playCount: null,
      accompleteIndex: 0,
    };
  },
  watch: {
    name() {
      if (name!==null) {
        this.isGetAllData = false;
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    // ...
    next((e) => {
      e.axios
        .get("/playlist/detail?id=" + to.query.id)
        .then((response) => {
          console.log(response.data.playlist);
          e.songs = response.data.playlist.trackIds;
          e.name = response.data.playlist.name;
          e.coverImgUrl = response.data.playlist.coverImgUrl;
          e.authorName = response.data.playlist.creator.nickname;
          e.authorImg = response.data.playlist.creator.avatarUrl;
          e.playCount = response.data.playlist.playCount;
          e.songs = e.songs
            .map((e) => {
              return e.id;
            })
            .toString();
          e.axios
            .get("/song/detail?ids=" + e.songs + "")
            .then((response) => {
              // console.log(response.data.songs);
              e.songs = response.data.songs;
              e.songs = e.songs.slice(0, 20);
            })
            .finally(() => {
              e.accompleteIndex++;

              console.log("songs", e.songs);
              // e.isGetAllData = false;
            });
        })
        .finally(() => {
          e.accompleteIndex++;
        });
      if (e.accompleteIndex === 2) {
        e.isGetAllData = false;
        e.accompleteIndex = 0;
      }
    });
  },
  methods: {
    hotSongUpLoadNowSong() {
      // 
      this.$emit("update:newSongList", this.songs);
    },
    updateCurrentSong(e, index) {
      console.log(":", e);
      // 
      this.$emit("update:currentSong", e, index);
    },
  },
};
</script>

<style lang="scss" scoped>
.song-list {
  width: 100%;
  .wraper {
    width: 100%;
    min-height: 100%;
    .list-top {
      min-height: 170px;
      width: 100vw;
      height: 30vh;
      padding: 30px 10px 30px 15px;
      overflow: hidden;
      position: relative;
      .header-Bg-wraper {
        width: 100vw;
        height: 31vh;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        filter: blur(20px);
        transform: scale(1.5);
        z-index: 2;
        &::after {
          content: " ";
          background-color: rgba(0, 0, 0, 0.25);
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          z-index: 1;
        }
      }
      .header-content-wraper {
        display: flex;
        position: relative;
        left: 0;
        top: 0;
        z-index: 2;
        .left {
          width: 120px;
          height: 120px;
          position: relative;
          img {
            width: 100%;
            vertical-align: middle;
          }
          span.dec {
            position: absolute;
            z-index: 3;
            top: 10px;
            left: 0;
            padding: 0 8px;
            height: 17px;
            color: #fff;
            font-size: 9px;
            text-align: center;
            line-height: 17px;
            background-color: rgba(217, 48, 48, 0.8);
            border-top-right-radius: 17px;
            border-bottom-right-radius: 17px;
          }
          span.play-count {
            font-size: 12px;
            position: absolute;
            right: 5px;
            top: 2px;
            color: #fff;

            &::before {
              content: "";
              display: inline-block;
              background: url("data:image/svg+xml;base64,");
              background-position: left center;
              background-repeat: no-repeat;
              background-size: auto 100%;
              width: 1.2em;
              height: 0.9em;
            }
          }
          &::after {
            content: " ";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 18px;
            z-index: 2;
            background-image: -webkit-linear-gradient(
              left,
              rgba(0, 0, 0, 0),
              rgba(0, 0, 0, 0.2)
            );
            background-image: linear-gradient(
              90deg,
              rgba(0, 0, 0, 0),
              rgba(0, 0, 0, 0.2)
            );
          }
        }
        .right {
          flex: 1 1 auto;
          width: 1%;
          margin-left: 16px;
          h2 {
            padding-top: 1px;
            font-size: 17px;
            line-height: 1.3;
            color: #fefefe;
            height: 44px;
            display: -webkit-box;
            -webkit-box-pack: center;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          .lsthd_auth {
            display: block;
            position: relative;
            margin-top: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: normal;
            .lsthd_link {
              display: inline-block;
              color: hsla(0, 0%, 100%, 0.7);
              .u-avatar {
                display: inline-block;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                vertical-align: middle;
                margin-right: 5px;
                img.u-img {
                  border-radius: 50%;
                  width: 100%;
                  vertical-align: middle;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
